     </div>
     </div>
        <div class="box">
      <button @click="showMorocco()">Morocco</button>
   <div v-if="infoMA">
    <p>Total Cases: {{infoMA.latest.confirmed}}</p>
    <p>Total deaths: {{infoMA.latest.deaths}}</p>
